
.page-header {
  padding: 4px;
  background-color: aliceblue;
  border-radius: 4px;

  display: flex;
  justify-content: space-between;
}

.page-header_nav {
  width: 150px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.page-header_see-console {
  padding: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.page-header_see-console p {
  margin: 0;
  padding: 0;
}

.page-header_code {
  display: grid;
}
